<template>
  <div>
    <div class="reply" v-for="(reply,indexq) in comment.reply" v-bind:key="indexq">
      <div class="reply-title">
        <span class="font-bold">{{reply.discussantName}}&nbsp;回复&nbsp;{{comment.discussantName}}&nbsp;{{reply.commentTime}}</span>
        <div class="pointer" style="float: right"><a @click="changeCommenter(reply.id, reply.discussantName, reply)"><i class="icondazhongicon05 iconfont font-bold font-large" :style="{ color: reply.iconColor }"></i>&nbsp;<span :style="{ color: reply.iconColor, fontWeight: '300', fontSize: '12px' }">回复</span></a></div>
      </div>
      <div class="reply-nr">
        <div>
          <span v-if="reply.stepName">#{{reply.stepName}}</span>
          <span v-if="reply.materialName">#{{reply.materialName}}</span>
          <div v-html="reply.content">{{reply.content}}</div>
        </div>
        <div v-if="flg" class="new-reply">
          <textarea v-if="reply.newReply" :placeholder="reply.place" class="el-textarea__inner" v-model="reply.newReplyContent" style="min-height: 100px"></textarea>
          <div v-if="reply.newReply" class="new-reply-btn pointer" @click="submitSonComment(reply)"><span>发布</span></div>
        </div>
      </div>
      <list :comment="reply"></list>
    </div>
  </div>
</template>
<script>
export default {
  name: 'List',
  props: {
    comment: {}
  },
  data () {
    return {
      flg: false,
      newReplyId: '',
      newReplyName: '',
      newReplyContent: ''
    }
  },
  methods: {
    changeCommenter (id, name, obj) {
      this.flg = false
      if (obj.newReply === null || obj.newReply === undefined) {
        obj.newReply = true
        obj.place = '回复' + name
        obj.newReplyId = id
        obj.newReplyName = name
        obj.newReplyContent = null
        obj.iconColor = '#6612cf'
      } else {
        obj.newReply = !obj.newReply
        if (obj.newReply === false) {
          obj.iconColor = 'black'
        } else {
          obj.iconColor = '#6612cf'
        }
      }
      this.flg = true
    },
    submitSonComment (obj) {
      this.$emit('submitSonComment', obj)
    }
  }
}
</script>
<style lang="scss">
  p{
    margin:unset;
  }
  .reply{
    background: #ddd;
    .reply-title{
      padding-top: 10px;
    }
    .reply-nr{
      border-bottom: 1px solid #e5e5e5;
      padding-top: 10px;
      padding-bottom: 10px;
      width: 100%;
    }
  }
  .new-reply-btn{
    width: 55px;
    height: 25px;
    line-height: 25px;
    background: #6612cf;
    text-align: center;
    border-radius: 15px;
    color: white;
    position: relative;
    margin-top: -30px;
    float: right;
    margin-right: 5px;
  }
</style>
